import React, { Component } from 'react';
import fetch from 'node-fetch';

import Page from '../components/page';
import AtomicCard from '../components/atomic-card';

import { GridList } from 'rmwc/GridList'

export default class News extends Component {
    static async getInitialProps() {
        var news = undefined;
        
        await fetch(`https://atomic-api.herokuapp.com/news`)
          .then(response => response.json())
          .then(json => {
            if ('error' in json) {
              //stats = {displayName: "Unknown"};
            } else {
              news = json;
            }
          })
          .catch(ex => {
            //stats = {displayName: "Unknown"};
          })
    
        return {news};
    }

    render() {
        return (
            <Page>
                <AtomicCard className="atomic-news" title="News" titleSize="headline3" titleColor="var(--mdc-theme-primary)" outlineColor="var(--mdc-theme-primary)" backgroundColor="var(--drawer-color)" width="calc(100% - 8px)" maxWidth="1150px">
                    <GridList>

                    </GridList>
                </AtomicCard>
            </Page>
        )
    }
}